import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';

/**
 * Generated class for the UserTicketsPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-tickets',
  templateUrl: 'user-tickets.html',
})
export class UserTicketsPage {
  user: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private actionSheetCtrl: ActionSheetController) {
    this.user = navParams.get('user');   
    console.log(this.user);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserTicketsPage');
  }
  presentActionSheet(ticket) {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Modify your '+ticket+'.',
      buttons: [
        {
          text: 'Destructive',
          role: 'destructive',
          handler: () => {
            console.log('Destructive clicked');
            const index = this.user.concerts.indexOf(ticket);
            this.user.concerts.splice(index,1);
            console.log(this.user);
          }
        }, {
          text: 'Archive',
          handler: () => {
            console.log('Archive clicked');
          }
        }, {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }
}



